*
{
    padding: 0;
    margin: 0;
}
.home_title
{
    text-align: center;
    color: yellowgreen;
}
.home
{
    max-width: 1000px;
    margin: 0 auto;
    margin-top: 90px;
    height: 100%;
}
.home_container
{
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.home_formcontainer
{
    box-shadow: 1px 4px 12px -2px rgba(0,0,0,4.3);
    background-color: rgb(92, 2, 2);
    padding: 30px;
    border-radius: 10px;
    width: 500px;
}
.home_formcontainer input
{
    border: 1px solid #e5e5e5;
    outline: none;
    padding: 15px 10px;
    width: 80%;
    border-radius: 10px;
    margin: 10px 0;
    background: transparent;
    color: white;
    font-size: 14px;
}
.home_formcontainer input::placeholder{
    color: white;
}
.home_formcontainer button
{
    background-color: transparent;
    border: 1px solid white;
    outline: none;
    font-size: 18px;
    color: white;
    padding: 10px;
    width: 200px;
    margin-top: 30px;
    margin-left: 50%;
    border-radius: 12px;
    cursor: pointer;
}