*
{
    padding: 0;
    margin: 0;

}
nav
{
    background-color: rgb(8, 8, 104);
    color: white;
    display: flex;
    padding: 20px;
    justify-content: space-between;
    align-items: center;
}
nav h1{
    font-size: 30px;
    color: white;
    font-weight: 500 ;
}
nav ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 30%;
}
nav ul a{
    font-size: 20px;
    font-weight: 300;
    color: white;
    text-decoration: none;
}
nav ul a:hover{
    color:limegreen;
}